<template>
    <div class="singerDetail">
        <i class="fa fa-angle-left" aria-hidden="true" @click="$parent.singerIntroduce = false"></i>
        <div class="singer-hd">
            <img class="hd-pc" :src="singerDetail.artist?.cover" alt="">
            <div class="singer-info">
                <p class="name">{{singerDetail.artist?.name}}</p>
                <div class="rank" v-if="singerDetail.artist?.rank">歌手榜华语地区NO.{{singerDetail.artist?.rank.rank}}</div>
                <div class="simple">
                    <div class="nickname">
                        昵称：{{singerDetail.user?.nickname}}
                        <img v-if="singerDetail.user?.avatarDetail"
                            :src="`${singerDetail.user.avatarDetail?.identityIconUrl}?imageView=1&type=webp&thumbnail=12x0`" />
                    </div>
                    <div class="follow">关注 22 | 粉丝 629.3万</div>
                    <div class="identities">
                        <span v-for="tag in singerDetail.artist?.identifyTag" :key="tag">{{tag}}</span>
                        <span class="its" v-for="(it,i) in singerDetail.artist?.identities" :key="i">{{it}}</span>
                    </div>
                </div>
            </div>
        </div>
        <h2>歌手简介</h2>
        <div class="intro">
            <p :class="{all: isclick}">{{singerDetail.artist?.briefDesc}}</p>
            <div class="all-intro" v-if="singerDetail.artist?.briefDesc.length > 100 && !isclick" @click="isclick=true">
                <span>完整歌手介绍</span>
                <i class="fa fa-angle-down" aria-hidden="true"></i>
            </div>
            <div class="all-intro" v-if="singerDetail.artist?.briefDesc.length > 100 && isclick" @click="isclick=false">
                <span>收起</span>
                <i class="fa fa-angle-up" aria-hidden="true"></i>
            </div>
        </div>
        <h2>热门单曲</h2>
        <ul>
            <li class="song-item" v-for="(song,i) in singetHotSongs" :key="i"
                @click="$emit('update-song',song); $emit('update-song-list', singetHotSongs)">
                <div class="ranking">{{i>=9?(i+1):'0'+(i+1)}}</div>
                <div class="song-box">
                    <div class="song">
                        <div class="song-name">
                            <span class="name">{{song.name}}</span>
                            <div class="alia" v-if="song.alia.length">
                                <span v-for="(alia,index) in song.alia" :key="index">({{alia}})</span>
                            </div>
                        </div>
                        <div class="song-introduce">
                            <i v-if="song.sq!==null"></i>
                            <span>{{song.ar[0].name}}</span>
                            <span v-if="song.ar.length>=2"> / {{song.ar[1].name}}</span>
                            <span> - {{ song.al.name }}</span>
                        </div>
                    </div>
                    <!-- <div class="song-play">
                        <span></span>
                    </div> -->
                    <div class="song-play" v-if="song.id !== songId">
                    </div>
                    <div class="song-play active" v-else :class="{playing:playing}">
                        <span>.</span>
                        <span>.</span>
                        <span>.</span>
                        <span>.</span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        singerDetail: Object,
        singetHotSongs: Array,
        playing: Boolean,
        songId: [Number, String]
    },
    data() {
        return {
            hotSongs: [],
            isclick: false,
        };
    },
};
</script>

<style lang="less" scoped>
.fa-angle-left {
    position: absolute;
    top: 10rem;
    left: 10rem;
    font-size: 24rem;
    color: white;
    z-index: 9;
}

.singerDetail {
    background-color: white;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 99999;
}

.singer-hd {
    width: 100%;
    height: 300rem;
    position: relative;
    background-color: white;

    .hd-pc {}

    &::after {
        content: "";
        background: rgba(0, 0, 0, .2);
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    .singer-info {
        position: absolute;
        left: 16px;
        bottom: 30px;
        color: #fff;
        z-index: 2;

        .name {
            font-size: 20px;
            line-height: 20px;
        }

        .rank {
            margin-top: 15px;
            font-size: 13px;
        }

        .simple {
            margin-top: 15px;
            opacity: .8;
            font-size: 12px;
            line-height: 12px;

            .nickname {
                img {
                    vertical-align: middle;
                }
            }

            .follow,
            .identities {
                height: 12px;
                margin-top: 10px;
            }

            .identities {
                &:nth-child(1) {
                    margin-right: 4px;
                }

                .its {
                    border: 1px solid #fff;
                    border-radius: 4px;
                    padding: 2px;
                    transform: scale(.7);
                    display: inline-block;
                    opacity: .6;
                }
            }
        }
    }

}

h2 {
    position: relative;
    font-size: 18px;
    z-index: 2;
    margin-top: -5px;
    padding: 15px;
    background: #fff;
    font-weight: 700;
}

.intro {
    background: #fff;
    padding: 0 16px 18px;
    position: relative;

    p {
        width: 344rem;
        color: #666;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 14rem;
        max-height: 60rem;
        white-space: wrap;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: normal;

        &.all {
            max-height: none;
        }
    }

    .all-intro {
        width: 100%;
        text-align: center;
        // background-color: #999;
        font-size: 14rem;
        margin-top: 20rem;

        span {
            margin-right: 4rem;
        }
    }
}

.song-item {
    margin-left: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .song-box {
        border-bottom: 1rem solid rgb(225, 222, 222);
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;
        position: relative;

        .song {
            padding: 6rem 6rem 6rem 0;
            // width: 300rem;

            .song-name {
                font-size: 17rem;
                padding-bottom: 4rem;
                display: flex;
                width: 290rem;

                .name {
                    white-space: nowrap;
                    // width: 100%;
                }

                .alia {
                    color: #888;
                    margin-left: 4rem;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    word-break: normal;
                }
            }

            .song-introduce {
                width: 280rem;
                font-size: 12rem;
                color: #888;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                word-break: normal;

                i {
                    display: inline-block;
                    width: 12rem;
                    height: 8rem;
                    margin-right: 4rem;
                    background: url(../assets/index_icon_2x.png) no-repeat;
                    background-size: 166rem 97rem;
                }
            }
        }

        // .song-play {
        //     padding: 0 10rem;

        //     span {
        //         display: inline-block;
        //         width: 22rem;
        //         height: 22rem;
        //         background: url(../assets/index_icon_2x.png) no-repeat;
        //         background-size: 166px 97px;
        //         background-position: -24px 0;
        //     }
        // }
    }

    .ranking {
        width: 40rem;
        height: 100%;
        font-size: 17rem;
        color: #888;
        margin-left: -10rem;
        text-align: center;
    }
}

@keyframes playingrunning {
    from {
        transform: rotateX(15deg);
    }

    to {
        transform: rotateX(60deg);
    }
}

.song-play {
    padding: 0 15rem;
    width: 22rem;
    height: 22rem;
    background: url(../assets/index_icon_2x.png) no-repeat;
    background-size: 166rem 97rem;
    background-position: -24rem 0;
    position: absolute;
    top: 50%;
    margin-top: -11rem;
    right: 10rem;

    &.active {
        background: none;
        display: flex;
        justify-content: space-around;

        span {
            width: 4rem;
            height: 100%;
            margin: 0 1rem;
            background-color: red;
            animation: playingrunning 0.4s infinite alternate;
            animation-play-state: paused;
            transform-origin: bottom;

            &:nth-child(1) {
                animation-delay: 0s;
            }

            &:nth-child(2) {
                animation-delay: -0.4s;
            }

            &:nth-child(3) {
                animation-delay: -0.2s;
            }

            &:nth-child(4) {
                animation-delay: -0.6s;
            }
        }

        &.playing {
            span {
                animation-play-state: running;
            }
        }
    }

}
</style>
