<template>
    <div class="m-tabct">
        <div class="tabct-item">
            <div class="m-hmsrch">
                <!-- 表单输入 -->
                <form action="">
                    <div class="input-cover">
                        <i class="srch-svg"></i>
                        <input type="text" placeholder="搜索歌曲、歌手、专辑" v-model="songValue" @focus="suggestArtists=null"
                            @keyup.enter="getSuggestSongs(songValue)">
                        <figure class="close" v-show="songValue" @click="clearValue">
                            <i class="empty-svg"></i>
                        </figure>
                    </div>
                </form>
                <!-- loading -->
                <div class="loading" v-if="loading"></div>
                <!-- 热门搜索 -->
                <div class="m-default" v-if="!songValue">
                    <section class="hot-list">
                        <h3>热门搜索</h3>
                        <ul class="list">
                            <li v-for="(hot,index) in hotSongs" :key="index"
                                @click="songValue=hot.first; getSuggestSongs(hot.first)">
                                {{hot.first}}</li>
                        </ul>
                    </section>
                </div>
                <!-- 搜索列表 -->
                <section class="m-recom" v-if="songValue && suggestArtists===null" v-show="!loading">
                    <h3 class="title">搜索“{{songValue}}”</h3>
                    <ul>
                        <SearchSong v-for="(song,index) in searchSong" :key="index" :song="song"
                            @click.native="getSuggestSongs(song.keyword);songValue=song.keyword"></SearchSong>
                    </ul>
                </section>
                <!-- 搜索历史 -->
                <section class="srch-history" v-if="!songValue" v-show="!loading">
                    <ul v-for="(history,index) in searchHistory" :key="index">
                        <li class="history-list" @click="getSuggestSongs(history.name);songValue=history.name">
                            <i class="clock-his"></i>
                            <div class="his-text">
                                <span>{{history.name}}</span>
                                <figure>
                                    <i class="u-empty-svg" @click.stop="deleteHistory(history.name);"></i>
                                </figure>
                            </div>
                        </li>
                    </ul>
                </section>
                <!-- 搜索结果 -->
                <div class="srch-result" v-if="songValue && suggestArtists!==null && !singerIntroduce"
                    v-show="!loading">
                    <section class="match-list">
                        <h3 class="title">最佳匹配</h3>
                        <ul>
                            <!-- 歌手 -->
                            <li class="match-item" v-for="sgArtists in suggestArtists" :key="sgArtists.id"
                                @click="singerIntroduce = true,getSingerIntro(sgArtists.id)">
                                <div class="ling-cover">
                                    <figure>
                                        <img :src="sgArtists.img1v1Url" alt="">
                                    </figure>
                                    <article>
                                        <h4>{{sgArtists.occupation}}:
                                            <p>{{sgArtists.name}}</p>
                                            <p v-if="sgArtists.trans"> ({{sgArtists.alias[0] || sgArtists.trans}})</p>
                                        </h4>
                                    </article>
                                    <i class="u-svg-arr"></i>
                                </div>
                            </li>
                            <!-- 歌单 -->
                            <li class="match-item" v-for="playlist in suggestPlayList" :key="playlist.name" @click="$router.push({path:'/playlist',query:{id:playlist.id}})">
                                <div class="ling-cover">
                                    <figure>
                                        <img :src="playlist.coverImgUrl" alt="">
                                    </figure>
                                    <article>
                                        <h4>歌单:
                                            <p>{{playlist.name}}</p>
                                        </h4>
                                        <p class="h-cover">by {{playlist.creator.nickname}}</p>
                                    </article>
                                    <i class="u-svg-arr"></i>
                                </div>
                            </li>
                        </ul>
                    </section>
                    <!-- 搜索结果列表 -->
                    <section class="song-list">
                        <ul>
                            <SearchSongList v-for="cdResult in cloundResult" :key="cdResult.id" :cdResult="cdResult" @update-song="$emit('update-song', $event)" @click.native="$emit('update-song-list', cloundResult)" :playing="playing" :songId="songId">
                            </SearchSongList>
                        </ul>
                    </section>
                </div>
                <!-- 歌手简介 -->
                <div class="singer" v-if="singerIntroduce">
                    <SingerDetails :singerIntroduce="singerIntroduce" :singerDetail="singerDetail" :singetHotSongs="singetHotSongs" @update-song="$emit('update-song', $event)" @update-song-list="$emit('update-song-list', $event)" :playing="playing" :songId="songId"></SingerDetails>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SearchSong from '@/components/SearchSong.vue';
import SearchSongList from '@/components/SearchSongList.vue';
import SingerDetails from '@/components/SingerDetails.vue';
// import { watch } from 'vue';
export default {
    props: {
        playing: Boolean,
        songId: [Number, String]
    },
    components: {
        SearchSong,
        SearchSongList,
        SingerDetails
    },
    data() {
        return {
            hotSongs: [],
            songValue: "",
            searchSong: [],
            searchHistory: JSON.parse(localStorage.getItem("searchHistory")) || [],
            cloundResult: [],
            suggestArtists: null,
            suggestPlayList: [],
            loading: false,
            singerIntroduce: false,
            singerDetail: {},
            singetHotSongs: [],
        };
    },

    methods: {
        clearValue: function () {
            // 添加搜索历史不能重复
            this.searchHistory = this.searchHistory.filter(o => this.songValue != o.name);
            this.searchHistory.unshift({ name: this.songValue });
            this.songValue = "";
        },
        deleteHistory: function (name) {
            this.searchHistory = this.searchHistory.filter(o => name != o.name);
        },
        getSuggestSongs: function (value) {
            this.loading = true;
            Promise.all([
                this.axios.get("https://apis.netstart.cn/music/cloudsearch?keywords=" + value),

                this.axios.get("https://apis.netstart.cn/music/search/multimatch?keywords=" + value),

            ]).then(([cloundRes, suggestRes]) => {
                this.loading = false;
                // console.log(suggestRes.data.result.albums);
                this.cloundResult = cloundRes.data.result.songs;
                this.suggestArtists = suggestRes.data.result.artist;
                // console.log(suggestRes.data.result);
                this.suggestPlayList = suggestRes.data.result.playlist;
            });
        },
        getSingerIntro: function (id) {
            Promise.all([
                this.axios.get("https://apis.netstart.cn/music/artist/detail", { params: { id } }),
                this.axios.get("https://apis.netstart.cn/music/artists", { params: { id } })
            ]).then(([sd, sh]) => {
                this.singerDetail = sd.data.data;
                // console.log(sh.data.hotSongs);
                this.singetHotSongs = sh.data.hotSongs.slice(0,5)
            });
        }
    },

    created() {
        this.loading = true;
        this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
            this.loading = false;
            // console.log(res.data.result.hots);
            this.hotSongs = res.data.result.hots;
        });
    },

    watch: {
        songValue: function (newValue) {
            if (newValue) {
                this.loading = true;
                this.axios.get("https://apis.netstart.cn/music/search/suggest?keywords=" + newValue +
                    "&type=mobile")
                    .then((res) => {
                        this.loading = false;
                        // console.log(res.data.result);
                        this.searchSong = res.data.result.allMatch;
                    });

            } else {
                this.searchSong = [];
            }
        },
        searchHistory: function (newHis) {
            // console.log(newHis);
            localStorage.setItem("searchHistory", JSON.stringify(newHis));
        },
    }
};
</script>

<style lang="less" scoped>
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.m-tabct {
    margin-top: 40rem;

    .tabct-item {
        .m-hmsrch {
            background: #fbfcfd;
            position: relative;

            form {
                padding: 15rem 10rem;
                position: relative;
                border-bottom: 1rem solid rgba(0, 0, 0, .1);

                .input-cover {
                    position: relative;
                    width: 100%;
                    height: 30rem;
                    padding: 0 30rem;
                    box-sizing: border-box;
                    background: #ebecec;
                    border-radius: 30rem;

                    .srch-svg {
                        position: absolute;
                        left: 0;
                        top: 9rem;
                        margin: 0 8rem;
                        vertical-align: middle;
                        width: 13rem;
                        height: 13rem;
                        background-image: url(../assets/search.svg);
                    }

                    input {
                        width: 100%;
                        height: 30rem;
                        line-height: 18rem;
                        background: rgba(0, 0, 0, 0);
                        font-size: 14rem;
                        color: #333;
                        border: none;

                        &:focus {
                            outline: none;
                        }
                    }

                    .holder {
                        position: absolute;
                        left: 32rem;
                        top: 5rem;
                        font-size: 14rem;
                        color: #c9c9c9;
                        background: rgba(0, 0, 0, 0);
                        pointer-events: none;
                    }

                    .close {
                        position: absolute;
                        right: 0;
                        top: 0;
                        width: 30rem;
                        height: 30rem;
                        line-height: 28rem;
                        text-align: center;

                        .empty-svg {
                            display: inline-block;
                            vertical-align: middle;
                            width: 14rem;
                            height: 14rem;
                            background-image: url(../assets/empty.svg);
                        }
                    }
                }
            }

            .loading {
                width: 50rem;
                height: 50rem;
                background: url(../assets/loading.gif) no-repeat;
                background-size: cover;
                position: fixed;
                top: 50%;
                left: 50%;
                margin-left: -25rem;
                margin-top: -25rem;
            }

            .hot-list {
                padding: 15rem 10rem 0;

                h3 {
                    font-size: 12rem;
                    line-height: 12rem;
                    color: #666;
                }

                .list {
                    margin: 10rem 0 7rem;

                    li {
                        display: inline-block;
                        height: 32rem;
                        margin-right: 8rem;
                        margin-bottom: 8rem;
                        padding: 0 14rem;
                        font-size: 14rem;
                        line-height: 32rem;
                        color: #333;
                        border: 1rem solid #d3d4da;
                        border-radius: 32rem;
                    }
                }
            }

            .m-recom {

                // width: 100%;
                .title {
                    height: 50rem;
                    margin-left: 10rem;
                    padding-right: 10rem;
                    font-size: 15rem;
                    line-height: 50rem;
                    color: #507daf;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;
                }

            }

            .history-list {
                height: 45rem;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .clock-his {
                    display: inline-block;
                    margin: 0 10rem;
                    width: 15rem;
                    height: 15rem;
                    background-image: url(../assets/clock.svg);
                }

                .his-text {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    height: 45rem;
                    flex-grow: 1;
                    border-bottom: 1rem solid rgba(0, 0, 0, .1);
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;

                    span {
                        margin-right: 10rem;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        word-break: normal;
                        font-size: 14rem;
                    }

                    figure {
                        width: 32rem;
                        height: 32rem;
                        line-height: 32rem;

                        .u-empty-svg {
                            display: inline-block;
                            margin-left: 2rem;
                            width: 12rem;
                            height: 12rem;
                            background-image: url(../assets/empty2.svg);
                        }
                    }
                }
            }

            .srch-result {
                background: #fcfcfd;

                .match-list {
                    padding: 8rem 0 4rem;

                    .title {
                        margin-left: 10rem;
                        font-size: 12rem;
                        line-height: 16rem;
                        color: #666;
                    }

                    .match-item {
                        position: relative;
                        height: 66rem;
                    }

                    .ling-cover {
                        display: flex;
                        align-items: center;
                        height: 66rem;
                        margin-left: 10rem;
                        padding: 8rem 10rem 8rem 0;
                        box-sizing: border-box;
                        position: relative;

                        figure {
                            width: 50rem;
                            height: 50rem;
                            margin-right: 15rem;
                            line-height: 0;
                            display: inline-block;

                            img {
                                display: block;
                                width: 100%;
                                height: 100%;
                            }
                        }

                        article {
                            display: flex;
                            flex-wrap: nowrap;
                            flex: 1;
                            display: inline-block;
                            width: 1%;

                            h4 {
                                width: 100%;
                                font-size: 17rem;
                                line-height: 30rem;
                                color: #333;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                white-space: nowrap;
                                word-break: normal;
                                display: inline-block;

                                p {
                                    display: inline;
                                }
                            }

                            .h-cover {
                                font-size: 12rem;
                                line-height: 15rem;
                                color: #999;
                                width: 100%;
                                display: inline-block;
                            }
                        }

                        .u-svg-arr {
                            margin-right: 8rem;
                            margin-left: 10rem;
                            display: inline-block;
                            width: 8rem;
                            height: 13rem;
                            background-image: url(../assets/arr.svg);
                        }
                    }
                }
            }

            .singer {
                width: 100%;
                height: 100%;
                position: absolute;
                top: -125rem;
                left: 0;
                z-index: 9999;
                background-color: #fff;
                padding-bottom: 70rem;
            }
        }
    }
}
</style>
